/**
 *  @param {string} time 
 *  @param {bool} friendly fromNow
 */
exports.textNum = function(data,num){
    if(data){
        return data.substring(0,num);
    }else{
        return null;
    }
    
}
/**
 *  @param {string} time 
 *  @param {bool} friendly fromNow
 */
exports.addTextTag = function(data,txt){
    if(data){
        return txt + ''+data;
    }else{
        return null;
    }
    
}


